import Loading from './brick/Loading'
import Toast from './brick/Toast'
import { Order } from './Order.js'
import {post,Timestamp} from "./method.js"
import localforage from 'localforage'

export default {
  data () {
    return {
      userName: '',
      password:'123456',
      promptText:':',
      selectList:null,
      needLog:true
    }
  },
  components: {
    Toast,
    Loading
  },
  created(){
    const _this = this
    const appData = {}
    localforage.getItem("appData",function(err,value){
      if( value == null ||  value.userData == null){

      }
    })
  },
  methods: {
    PreLogin: function(){ //
      const _this    = this,
            userName = this.userName,
            password = this.password;
      //
      if(userName === "" || password === ""){ Order.$emit('Toast', '') }
      else{
        // userName : 
        // password : 
        const postData = {userName:_this.userName,password:_this.password};
        Order.$emit('Loading', 'show')
        //
        new QWebChannel(navigator.qtWebChannelTransport, function(channel) {
          const foo = channel.objects.content;
          foo.callback.connect(function(receive) {
            Order.$emit('Loading', 'hide')
            //
            if(receive !=="" && receive !==null){
                const Data = JSON.parse(receive);
                switch(Data.length){
                case 0  : Order.$emit('Toast', ''); break; 
                //
                case 1  : const data = Data[0]; _this.login(data.usbkeyname,0,data.usbkeyidentification,data.unitId); break;
                default : _this.promptText = ':'; _this.selectList=Data;
                }
            }
            else{
                Order.$emit('Toast', '')
            }
          });
          foo.preLogin( JSON.stringify(postData))
        });
      }
    },
    login:function(name,num,idCard,unitId){ //
      const _this    = this,
            postData = {usbkeyidentification:idCard,password:this.password,unitId:unitId,userName:name};
      Order.$emit('Loading', 'show')
      new QWebChannel(navigator.qtWebChannelTransport, function(channel) {
        Order.$emit('Loading', 'hide')
        const foo = channel.objects.content;
        foo.callback.connect(function(receive) {
          const Data = JSON.parse(receive);
          if(Data.code == 0 || Data.code == 113){
            //
            const nowTime = new Date().getTime()
            const appData ={
              // userName : 
              // idCard   : 
              // key      : ID
              userData:{ userName:name, idCard:idCard, key:unitId },
              Timestamp: nowTime
            }
            Timestamp.value = nowTime
            //
            localforage.setItem('appData', appData,function (err){
                if(err){
                  Order.$emit('Toast', '')
                }
                else{
                  window.location.href="#/Main"
                }
            });
          }
          else{
            Order.$emit('Toast', '')
            _this.selectList = null
          }
        });
        foo.login(JSON.stringify(postData))
      });
    }
  },
}



