"use strict";
const get = function(url,fn){
  const obj=new XMLHttpRequest();  // XMLHttpRequest          
  obj.open('GET',url,true);
  obj.onreadystatechange = function(){
    if (obj.readyState === 4) { // readyState==4
      fn.call(this, obj.responseText);  //
    }
  };
  obj.send(null);
};

//
const cutString = function(original,before,after,index){
  index = index || 0;
    if (typeof index === "number") {
      const P = original.indexOf(before, index);
      if (P > -1) {
        if (after) {
          const f = original.indexOf(after, P + 1);
          return (f>-1)? original.slice(P + before.toString().length, f):
          console.error("owo [  "+after+"]");
        } 
        else {
          return original.slice(P + before.toString().length);
        }
      } 
      else {
        console.error("owo [  " + before + "]");
      }
    } 
    else {
      console.error("owo [sizeTransition:" + index + "!]");
    }
};

const post = function (url,data,fn) {
  const postData = JSON.stringify(data);
  const obj = new XMLHttpRequest();
  obj.open("POST", url, true);
  obj.setRequestHeader("Content-type", "application/x-www-form-urlencoded"); // 
  obj.onreadystatechange = function () {
    if (obj.readyState === 4 ) {  // 304
      fn.call(this, obj.responseText);
    }
  };
  obj.send(postData);
};

let Timestamp = {value:null};

let orgData = {
  orgList : {},
  orgTree : []
};

export {get, cutString, post, Timestamp, orgData};


