
import Swiper from 'vux/src/components/swiper/swiper.vue'

import AppTitle from './brick/AppTitle'
import TitleBar from './brick/Title'
import BottomBar from './brick/Bottom'
import Toast from './brick/Toast'
import { Order } from './Order.js'
import { post, Timestamp} from "./method.js" 
import localforage from 'localforage'

import Vue from 'vue';
import VueTouch from 'vue-touch';
Vue.use(VueTouch, {name: 'v-touch'});
//
const $tiangongyuanyuan = require('../assets/tiangongyuanyuan.png'),
      $xinxifabu        = require('../assets/xinxifabu.png'),
      $youjian          = require('../assets/youjian.png'),
      $bangongxitong    = require('../assets/bangongxitong.png');
export default {
  components: {
    Swiper,
    AppTitle,
    TitleBar,
    BottomBar,
    Toast
  },
  data () {
    return {
      index: 0,
      showList: [],
      userData:null,
      appList  : {
        tiangongyuanyuan:{
          id: "10000", 
          name: "", 
          icon: $tiangongyuanyuan,
          url: '', 
          special: "open", 
          type: "communication",
          detail:":1.41",
          isSelect: false,
          available: true,
          exist:true
        },
        bangongxitong:{
          id:"10004", 
          name:"", 
          icon:$bangongxitong,
          url:'', 
          special:"url", 
          type:"office",
          detail:":0.2",
          isSelect:false,
          available: false,
          exist:true
        },
        youjian:{
          id: "10002", 
          name: "", 
          icon: $youjian,
          url: '', 
          special: "url", 
          type: "office", 
          detail:":1.6",
          isSelect: false,
          available: true,
          exist:true
        },
        xinxifabu:{
          id: "10001", 
          name: "", 
          icon: $xinxifabu, 
          url: 'http://info.casic.cs/jeecms2/index/mobile/', 
          special: "url", 
          type:"office" ,
          detail:":0.8",
          isSelect: false,
          available: true,
          exist:true
        }
      },
      showDelateButton: false,//
      selectNumber:0
    }
  },
  created(){
    const _this = this;
    localforage.getItem("appData",function(err,appData){
      const nowTime = new Date().getTime()
      if(nowTime - Timestamp.value > 1200000){
        window.location.href="#/TimeOut";
        return null
      }
      Timestamp.value = nowTime
      const userData = appData.userData
      _this.userData = userData
      //
      if( appData !== null && appData.showList != null){
        _this.showList = appData.showList
      }
      else{ 
        //**  ** 1. 2.
        // {type:5}
        new QWebChannel(navigator.qtWebChannelTransport, function(channel) {
          const foo = channel.objects.content;
          foo.callback.connect(function(receive) {
            if(receiveData !=="" && receiveData !==null){
              const Data = JSON.parse(receiveData);
              _this.showList = Data
              appData.showList = Data
              localforage.setItem('appData', appData,function (err){
                if(err){
                  Order.$emit('Toast', '')
                }
              });
            }
            else{ //URL
              //
              _this.showList = [
                {url: 'http://www.casic.com.cn/n101/index.html',img: 'http://puge-10017157.cossh.myqcloud.com/tianzhi/c.png',title: ''},
                {url: 'http://www.casic.com.cn/n101/index.html', img: 'http://puge-10017157.cossh.myqcloud.com/tianzhi/d.png', title: ''}
              ]
              Order.$emit('Toast', '')
            }
          });
          foo.slidesshow(JSON.stringify({type:"5"}))
        })
      }
      //----------------------------------------------------------------------------------------------------
      if(appData.appList != null){
        _this.appList = appData.appList
      }
      const BanGongURL = 'http://10.152.36.26:8080/portal/menu.jsp?userName='+userData.userName+'&PID='+userData.idCard+'&webService=&SessionID='
      // 1   available false
      if(userData.key == "1"){
        _this.appList["bangongxitong"].url = BanGongURL
        _this.appList["bangongxitong"].available = true
      }
      _this.appList["youjian"].url = 'http://10.152.36.31/secmail/loginapp.do?type=cid&PID='+userData.idCard
      appData.appList = _this.appList
      //--------------------------------------------------------------------------------------------------------------
      // ** 
      localforage.setItem('appData', appData,function (err){
        if(err){
          Order.$emit('Toast', '')
        }
      });
    })
  },
  methods: {
    onIndexChange: function(index) { //
      this.index = index
    },
    openApp: function () { //
      const app1 = {
        "type":2,
        "sopid":"com.vrv.linkDood",
        "pkgpath":"com.vrv.linkDood-1.0.45.sop",
        "scheme":"linkdood:showlinkdood?id=" + this.userData.idCard,
        "name":"linkdood"
      };
      //
      new QWebChannel(navigator.qtWebChannelTransport, function(channel) {
        const foo = channel.objects.content;
        foo.opensopApp(JSON.stringify(app1))
      });
    },
    openStart:function(url,special,key){ //
      const _this = this
      //
      if(this.appList[key].isSelect === true){
        // 
        this.appList[key].isSelect = false
        //1
        this.selectNumber--
        if(this.selectNumber === 0){
          // 
          this.showDelateButton = false
        }
      }
      else{
        //
        if(this.showDelateButton === true){
          //
          this.appList[key].isSelect = true
          //1
          this.selectNumber++
        }
        else{
          switch(special){
            case 'open':this.openApp();break; // 
            case 'url':window.location.href=url;break; //Url 
          }
        }
        
      }
      
    },
    pressItem:function(key){ //app
      //appItem
      this.appList[key].isSelect = true
      //
      this.showDelateButton = true
      //1
      this.selectNumber++
    },
    delateApp:function(){
      const oldList = this.appList,
            _this   = this;
      let   mark    = false ;//app
      for(let item in oldList){
        //Jsonmark
        if(oldList[item].isSelect){ 
          //
          oldList[item].exist = false
          //
          oldList[item].isSelect = false
          mark = true
        }
      }
      //mark
      if(mark) {
        //
        localforage.getItem("appData",function(err,appData){
          appData.appList = _this.appList
          localforage.setItem('appData', appData,function (err){
            if(err){
              Order.$emit('Toast', '')
            }
          });
        })
      }
      //
      _this.showDelateButton = false
    }
  }
}



